import { loginAsync } from '../redux/features/auth/actions';
import { store } from '../redux/store';
import { NavigationService } from '../services/NavigationService';
import { SCREENS_ENUM } from '../types/global';
import { logger } from '../utils';

export interface FormLogin {
  username: string;
  password: string;
}
export const onCLickLogin = (values: FormLogin) => {
  try {
    const { username, password } = values;
    if (!username || !password) {
      return;
    }
    store.dispatch(loginAsync({ username, password }));
  } catch (error) {
    logger('error onCLickLogin', error);
  }
};

export const handleForgotPassword = () => {
  NavigationService.navigate(SCREENS_ENUM.ForgotPasswordScreen);
};
